

#text {
    padding: 5px;
    display: inline-block;
    width: 75%;
    height: 45px;
    margin: 5px;
    font-size: 190%;
    border-radius: 20px;
    border:  0.4vh solid #1613b4f5;
    background-color: rgb(0, 247, 255);
}
body {
    background-image: url("https://dm0qx8t0i9gc9.cloudfront.net/thumbnails/video/rN0W64K4ipau8gxv/videoblocks-colorful-gradient-background-seamless-live-wallpaper-4k-abstract-colorful-background_hzmnrek-u_thumbnail-1080_01.png");
}

button{
    padding: 10px;
    display: inline-block;  
    border-radius: 25px;
    border: 2px dashed blue;
    width: 20vh;
    height: 8vh;
}
#add{
    color: green;
    font-weight: bolder;
}
#clear{
    color: red;
    font-weight: bolder;
}






.div_co {
    display: inline;

    margin-top: 5px;
    width: 33.33%;
    float: left;
    font-size: 5vh;

}

.div_todo{
    display: inline;
    width: 45px;
    height: 15px;
    /*background: #555;*/
    margin: 20px 10px;
    position: relative;
    border-radius: 5px;
}

.div_todo label{
    min-width: 30vh;
    background-color: rgba(255, 157, 157, 0.1);
    display: inline-block;
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
    border: 3px ridge #000;
    text-align: center;
    box-shadow: 0px 0px 8px red
}


  
